
.custom-left-menu {
  border-top-left-radius: 10px; /* Make top left corner circular */
  border-top-right-radius: 10px; /* Make top right corner circular */
  border-bottom-left-radius: 10px; /* Make bottom left corner circular */
  border-bottom-right-radius: 10px; /* Make bottom right corner circular */
  background-color: #f8f9fa;
  font-size: 1.1rem;
}
.dark-mode .custom-left-menu {
  border-top-left-radius: 10px; /* Make top left corner circular */
  border-top-right-radius: 10px; /* Make top right corner circular */
  border-bottom-left-radius: 10px; /* Make bottom left corner circular */
  border-bottom-right-radius: 10px; /* Make bottom right corner circular */
  background-color: #6f7070;
  font-size: 1.1rem;
  color:white
}
.custom-left-menu:hover {
  background-color: lightgray; /* Change background color to gray */
  cursor: pointer; /* Change cursor to pointer on hover */
}

.topRuler {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px; /* adjust height as needed */
  background-color: #ffffff; /* light gray */
  border-bottom: 1px solid #ccc; /* border line */
  z-index: 1000; /* ensure it's above other content */
}
.dark-mode .topRuler {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px; /* adjust height as needed */
  background-color: #6f7070;/* light gray */
  border-bottom: 1px solid #ccc; /* border line */
  z-index: 1000;
  color: white; /* ensure it's above other content */
}
.content {
  margin-top: 60px;
  background-color: #f8f9fa;
}
.dark-mode .content {
  margin-top: 60px;
    background-color: #6f7070;
  
}

.custom-facebook-icon {
  font-size: 2.6rem; /* Adjust the size as needed */
}

.custom-middle-icons {
  font-size: 1.8rem; /* Adjust the size as needed */
}

.custom-middle-icons .col:hover {
  background-color: lightgray; /* Change to your desired background color */
  cursor: pointer;
}

.custom-middle-icons .col {
  border-top-left-radius: 10px; 
    border-top-right-radius: 10px; 
    border-bottom-left-radius: 10px; 
    border-bottom-right-radius: 10px; 
}

.gray-circle {
  width: 47px;
  height: 45px;
  background-color: rgb(180, 180, 180);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  margin-top: 2px;
  cursor: pointer; /* Optional: Add cursor pointer for user interaction */
}
